html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-family: "Segoe UI", sans-serif;
  background: linear-gradient(to bottom, #e6fffa, #ffffff);
  color: #333;
}

.app-layout {
  display: flex;
}

li, ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

/* ────── 사이드 네비게이션 ────── */
.side-nav {
  position: fixed;
  top: 0;
  left: 0;
  width: 250px;
  height: 100vh;
  background: #ffffff;
  box-shadow: 2px 0 8px rgba(0, 0, 0, 0.05);
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 60px 20px;
  gap: 20px;
  z-index: 999;
}

.nav-link {
  font-weight: 600;
  font-size: 1.1rem;
  padding: 12px 16px;
  border-radius: 8px;
  transition: all 0.2s ease;
}

.nav-link:hover {
  background-color: #e0fdfa;
  color: #0b7c69;
}

.nav-link.active {
  background-color: #e0fdfa;
  color: #0b7c69;
  font-weight: bold;
  border-left: 4px solid #60e1cb;
}

/* ────── 메인 컨텐츠 ────── */
.main-content {
  margin-left: 250px;
  padding: 60px 40px;
  flex-grow: 1;
  box-sizing: border-box;
}

/* ────── 공통 ────── */
.section {
  padding: 100px 0;
  border-bottom: 1px solid #eee;
  height: 780px;
}

/* ────── 홈 ────── */
#home {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100vh;
}

.hero {
  max-width: 800px;
  margin: 0 auto;
}
.hero h1 {
  font-size: 3.5rem;
  margin-bottom: 20px;
}
.hero p {
  font-size: 1.3rem;
  color: #555;
  margin-bottom: 30px;
  line-height: 1.6;
}
.highlight {
  color: #60e1cb;
}
.btn {
  display: inline-block;
  padding: 12px 24px;
  background-color: #60e1cb;
  color: white;
  border-radius: 25px;
  font-weight: bold;
  transition: background 0.3s ease;
}
.btn:hover {
  background-color: #4fcab3;
}

/* ────── About ────── */
.about {
  max-width: 800px;
  margin: 0 auto;
  font-size: 1.15rem;
  line-height: 1.8;
}
.about h2 {
  font-size: 2.2rem;
  margin-bottom: 20px;
  color: #111;
}
.about p {
  color: #444;
}

/* ────── Skills ────── */
.skills {
  margin-top: 20px;
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}
.skills span {
  background-color: #dafaf3;
  color: #0e8670;
  padding: 8px 16px;
  border-radius: 30px;
  font-size: 1rem;
  font-weight: 600;
}

/* ────── 프로젝트 ────── */
.projects {
  max-width: 800px;
  margin: 0 auto;
}
.projects h2 {
  font-size: 2.2rem;
  margin-bottom: 30px;
}
.project-sub {
  font-size: 0.95rem;
  color: #888;
  margin-bottom: 10px;
}
.project-desc {
  color: #555;
  margin-bottom: 15px;
  line-height: 1.6;
}
.feature-list {
  padding-left: 20px;
  margin-bottom: 15px;
}
.feature-list li {
  margin-bottom: 8px;
}
.feature-list li::before {
  content: '✔ ';
  color: #60e1cb;
  font-weight: bold;
}

/* ────── Contact ────── */
.contact {
  max-width: 600px;
  margin: 0 auto;
  text-align: center;
}
.contact h2 {
  font-size: 2rem;
  margin-bottom: 20px;
}
.contact p {
  margin: 10px 0;
  font-size: 1.1rem;
}
.contact a {
  color: #60e1cb;
  font-weight: 600;
}
.contact a:hover {
  text-decoration: underline;
}

/* ────── 푸터 ────── */
.footer {
  margin-top: 60px;
  text-align: center;
  font-size: 0.9rem;
  color: #777;
}

/* ────── 반응형 ────── */
@media (max-width: 768px) {
  .app-layout {
    flex-direction: column;
  }
  .side-nav {
    flex-direction: row;
    height: auto;
    width: 100%;
    justify-content: space-around;
    padding: 10px 0;
    position: relative;
  }
  .main-content {
    margin-left: 0;
    padding: 20px;
  }
  .section {
    padding: 60px 20px;
  }
  .hero,
  .about,
  .projects,
  .contact {
    padding: 0 10px;
  }
}
